.white-label {
    section {
        padding: 60px 0;
        @include breakpoint(mobile){
            padding: 30px 0;
        }
    }
    .partner-header {
        .logo {
            text-align: center;
            img {
                margin: 15px auto;
                @include breakpoint(mobile){
                    max-width: 250px;
                }
            }
        }
        .logo-partner {
            border-right: solid 2px #ccc;
            @include breakpoint(mobile){
                border-right: none;
                border-bottom: solid 2px #ccc;
                img {
                    @include breakpoint(mobile){
                    max-width: 250px;
                    }

                }
            }
        }
    }
    .partner-hero {
        background-color: #F7F6F8;
        padding-top: 0;
        .welcome {
            background-color: $white;
            h1 {
                font-size: 65px;
            }
            img {

            }
        }
        .content {
            padding: 60px 0;
            h2 {
                font-size: 60px;
                @include breakpoint(mobile){
                    width: 100%;
                    font-size: 24px;
                }
            }
            a.btn {
                font-size: 34px;
                @include breakpoint(mobile){
                    font-size: 24px;
                }
            }
        }
    }
    .partner-intro {
        .partner-block {
            padding: 30px;
            margin: 50px 0;
            border-radius: 8px;
            background-color: $white;
            box-shadow: 0 0 10px $black15;
            .inner {
                display: flex;
                flex-flow: row wrap;
                justify-content: space-between;
                align-items: center;
                .image {
                    width: calc(33% - 30px);
                    @include breakpoint(mobile){
                        width: 100%;
                    }
                    img {
                        max-width: 100%;
                        margin: 15px;
                        @include breakpoint(mobile){
                            margin: 0 auto;
                        }
                    }
                }
                .content {
                    width: 66%;
                    font-size: 23px;
                    @include breakpoint(mobile){
                        width: 100%;
                        font-size: 18px;
                    }
                    > * {
                        padding: 15px;
                        margin: 0;
                    }
                }
            }
            &.logo-right {
                .inner {
                    flex-flow: row-reverse wrap;
                }
            }
        }
        .pullquote {
            margin: 50px 0 0;
            .inner {
                display: flex;
                flex-flow: row wrap;
                justify-content: space-between;
                align-items: center;
                .image {
                    width: 33%;
                    @include breakpoint(mobile){
                        width: 100%;
                    }
                    img {
                        max-width: 100%;
                    }
                }
                .content {
                    width: 63%;
                    font-size: 24px;
                    color: #666;
                    @include breakpoint(mobile){
                        width: 100%;
                    }
                    span.quote {
                        display: block;
                        color: #23bfee;
                        font-size: 150px;
                        font-weight: 900;
                        line-height: 25px;
                    }

                } 
            }
            &.image_right {
                .inner {
                    flex-flow: row-reverse wrap;
                    .content {
                        span.quote {
                            text-align: right;
                        }
                    }
                }
            }
            &.no_image {
                .inner {
                    .image {
                        display: none;
                    }
                    .content {
                        width: 100%;
                        span.quote {
                            text-align: right;
                        }
                    }
                }
            }
        }

    }
    .partner-states {
        background-color: #F7F6F8;
        .content {
            @include breakpoint(mobile){
                text-align: center;
            }

        }
    }
    .partner-products {
        .product-grid {
            justify-content: center;
            .content {
                .extra {
                    ul {
                        padding-left: 0;
                        list-style: none;
                        li {
                            list-style: none;
                            &:before {
                                content: '';
                                display: inline-block;
                                position: relative;
                                top: 7px;
                                height: 28px;
                                width: 28px;
                                margin-right: 7px;
                                background-image: url('https://go.dreamspring.org/hubfs/check.jpg');
                                background-repeat: no-repeat;
                                background-size: cover;
                            }
                        }
                    }
                }
            }
            .button {
            }
        }
    }
    .partner-questions {
        #hs_cos_wrapper_partner_form {
            .inputs-list.multi-container {
                list-style: none;
                padding-left: 0;
                display: flex;
                flex-flow: row wrap;
                justify-content: space-around;
                li {
                    label {
                    }
                }
            }
            .hs-error-msgs {
                padding-left: 0;
                list-style: none;
                li {
                    background-color: $warning;
                    .hs-error-msg {
                        display: block;
                        padding: 15px;
                        font-weight: bold;
                    }
                }
            }
        }
    }

}